@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,500;0,600;0,700;0,800;1,400;1,500;1,600;1,700;1,800&display=swap');

.enclosure{
    width: 100%;
    height: min-content;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.Calctitle{
    font-size: 25px;
    font-weight: 600;
    width: 100%;
    display: flex;
    align-items: center;
    text-align: center;
    justify-content: center;
    font-family: 'Montserrat';
}

.calc-container{
    display: flex;
    flex-grow: 1;
    width: 100%;
    height: 100%;
    flex-direction: column;
    margin: 20px;

    background-color: #0d0d30;
    border-radius: 10px;
}

.input-output-side{
    display: flex;
    flex-direction: 2;
    flex-direction: column;
    margin: 10px;
    padding: 10px;

    border-radius: 5px;
    background-color: #d2dbff;
    justify-content: center;
    align-items: center;
}

.numInput{
    margin: 5px;
    border-radius: 5px;
    width: 100%;
    height: 30px;

    background: none;
    border: none;
    color: #000;
    font-family: 'Montserrat';
    font-size: 12px;
    font-weight: 500;

    transition-duration: 0.5s;
}

.small-screen{
    margin: 5px;
    border-radius: 5px;
    width: 100%;

    background-color: none;
    color: #000;
    font-family: 'Montserrat';
    font-size: 12px;
    font-weight: 500;

    transition-duration: 1s;
}

.operator-submit-side{
    display: flex;
    flex-direction: 3;
    flex-direction: column;
    padding: 10px;
}

.racks{
    width: 100%;
    height: 33%;
    margin: 2px;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.button-grid{
    display: flex;
    margin-top: 5px;
    margin-left: 10px;
    margin-right: 10px;
    margin-bottom: 5px;
    align-items: center;
    width: 50%;
    justify-content: center;

    height: 40px;
    width: 40px;
    border: none;
    border-radius: 25px;

    color: black;
    background: none;
    border-radius: 0px;
    background-color: #205b7a;

    font-family: 'Montserrat';
    font-weight: 800;
    font-size: 15px;
    text-decoration: none;
}

.btn-rounded { border-radius: 25px }

.controls{
    width: 100%;
    margin: 6px;
    display: flex;
    flex-direction: row;
}

.v2{
    width: 100%
}
