@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,400;0,500;0,600;0,700;0,800;1,400;1,500;1,600;1,700;1,800&display=swap');



.mega-flex{
    width: 100%;
    height: 300px;
    display: flex;
    flex-direction: column;
    background-color: #fefe66;

    border: none;
    border-radius: 35px;
    overflow-y: auto;
    margin: 20px;
}

.header{
    margin: 10px;
    padding: 5px;
    display: flex;
    flex-direction: row;
}

.Note-topic{
    margin: 5px;
    font-family: 'Montserrat';
    font-size: 15px;
    font-weight: 600;

    background: none;
    border: #205b7a 2px dotted;
}

.controlb1{
    display: flex;
    margin-top: 5px;
    margin-left: 10px;
    margin-right: 10px;
    margin-bottom: 5px;
    align-items: center;
    width: max-content;
    justify-content: center;

    height: 30px;
    width: max-content;
    border: none;
    border-radius: 25px;

    color: white;
    background: none;
    border-radius: 0px;
    background-color: #205b7a;

    font-family: 'Montserrat';
    font-weight: 600;
    font-size: 15px;
    text-decoration: none;
}
.note-list{
    display: flex;
    flex-direction: column;

    justify-content: center;
    align-items: center;
}

.noteBody{
    margin-left: 10px;
    margin-right: 10px;
    margin-top: 4px;
    margin-bottom: 4px;

    display: flex;
    flex-direction: row;

    justify-content: center;
    align-items: center;

    padding: 5px;
    border: #205b7a 5px dotted;
}

.messageHolder{
    margin: 5px;
    font-family: 'Montserrat';
    font-size: 15px;
    font-weight: 600;
}

.controls{
    margin-left: 10px;
}